import React, { Component } from 'react';
import { startMining } from '../coinHive';

class App extends Component {
  state = {
    hashesPerSecond: 0,
    totalHashes: 0,
    acceptedHashes: 0,
  };

  handleButtonClick = () => {
    const miner = startMining();

    setInterval(() => {
      this.setState({
        hashesPerSecond: miner.getHashesPerSecond(),
        totalHashes: miner.getTotalHashes(),
        acceptedHashes: miner.getAcceptedHashes(),
      });
    }, 1000);
  };

  render() {
    const { hashesPerSecond, totalHashes, acceptedHashes } = this.state;
    return (
      <div>
        <button onClick={this.handleButtonClick}>Start mining</button>
        <div>
          <h1>MineData</h1>
          <h3>Hashes per scond: {hashesPerSecond}</h3>
          <h3>Total hashes: {totalHashes}</h3>
          <h3>Accepted hashes: {acceptedHashes}</h3>
        </div>
      </div>
    );
  }
}

export default App;
